#Header_Xtra_sticky_n_gooey {
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	left: 0;
	height: 130px;
	padding-top: 5px;
	padding-left: 20px;
	color: aliceblue;
	-webkit-transition: all 0.5s ease;
	transition: all 0.5s ease;
}

#mainContainer {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;

	height: 90%;
	z-index: 40;
	color: aliceblue;
}
.Creamy {
	-webkit-transition: all 0.5s ease;
	transition: all 0.5s ease;
	background: #100101;
	-webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.75);
	box-shadow: 0px 0px 50px 0px rgba(87, 3, 3, 0.75);
}
#stickyLogo {
	width: 100px;
}
#infoContainer {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin-top: -10vh;
}
#thugwareCenter {
	font-size: 5rem;
	color: rgb(162, 126, 133);
}
